"use strict";

const router_ts_1 = require('./utils/router.ts');
const Common_ts_1 = require("./routes/Common.ts");
const Home_ts_1 = require("./routes/Home.ts");
const Products_ts_1 = require("./routes/Products.ts");
const jquery = require('jquery');
const $ = jquery;
(function () {
    class App {
        constructor() {
            // Use this variable to set up the common and page specific functions. If you
            // rename this variable, you will also need to rename the namespace below.
            this.routes = {
                Common: Common_ts_1.default,
                Home: Home_ts_1.default,
                Products: Products_ts_1.default
            };
        }
        init() {
            new router_ts_1.default(this.routes).loadEvents();
        }
    }
    let bootstrap = new App();
    //use window.load for now - dev site loads too fast and doc.ready doesnt work - but it works when not in dev env
    $(window).load(function () {
        console.log("Doc loaded");
        bootstrap.init();
    });
    //test3
})();


