uto;\n    top: 50%;\n    @include transform(translateX(-50%) translateY(-50%));\n  } @else if $xy == x {\n    left: 50%;\n    right: auto;\n    @include transform(translateX(-50%));\n  } @else if $xy == y {\n    bottom: auto;\n    top: 50%;\n    @include transform(translateY(-50%));\n  }\n}\n\n@mixin box-shadow($shadow...) {\n  -webkit-box-shadow: $shadow; // iOS <4.3 & Android <4.1\n  box-shadow: $shadow;\n}\n\n@mixin font-smoothing {\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\n@mixin animation--fadedown($delay) {\n  // Apply the fadeIn keyframes. Each animation\n  // will take 0.3s and have an ease-in-out\n  @include animation(fadeIn .3s ease-in-out);\n  // This animation should only play once\n  @include animation-iteration-count(1);\n  // Make sure the element maintains it's\n  // final visual state (i.e. 100% opacity)\n  @include animation-fill-mode(forwards);\n  // Delay - don't start the animation until we say so\n  @include animation-delay(#{$delay}s);\n}\n\n// Bem selectors\n\n@mixin e($element) {\n  &__#{$element} {\n    @content;\n  }\n}\n\n//modifier\n@mixin m($modifier) {\n  &.#{$modifier} {\n    @content;\n  }\n}\n\n\n/* ----------------------------------------------------------------------------\n * Breakpoints\n *\n\n * ------------------------------------------------------------------------- */\n@mixin breakpoint($point) {\n  @if $point == desktopXL {\n    @media only screen and (min-width: 1600px) {\n      @content;\n    }\n  } @else if $point == desktop {\n    // Extra large. Above 75em (1200px)\n    @media only screen and (min-width: $desktop) {\n      @content;\n    }\n  } @else if $point == laptop {\n    // Large. Above 62em (992px)\n    @media only screen and (min-width: $laptop) {\n      @content;\n    }\n  } @else if $point == tablet {\n    // Medium. Above 48em (768px)\n    @media only screen and (min-width: $tablet) {\n      @content;\n    }\n  } @else if $point == mobile {\n    // Small. Above 34em (544px)\n    @media (min-width: $mobile) {\n      @content;\n    }\n  } @else if $point == laptop-rd {\n    // Medium.max width 1024\n    @media only screen and (min-width: 1025px) {\n      @content;\n    }\n  } @else if $point == rd-mobile-v-max {\n    // Medium.max width 1024\n    @media only screen and (max-width: 480px) {\n      @content;\n    }\n  }@else if $point == rd-mobile-h-max {\n    // Medium.max width 1024\n    @media only screen and (max-width: 600px) {\n      @content;\n    }\n  } @else if $point == rd-tablet-max {\n    // Medium.max width 1024\n    @media only screen and (max-width: 768px) {\n      @content;\n    }\n  } @else if $point == rd-laptop-max {\n    // Medium.max width 1024\n    @media only screen and (max-width: 1024px) {\n      @content;\n    }\n  } @else if $point == rd-desktop-max {\n    // Medium.max width 1024\n    @media only screen and (max-width: 1200px) {\n      @content;\n    }\n  } @else if $point == rd-desktopXL{\n    // Medium.max width 1024\n    @media only screen and (min-width: 1300px) {\n      @content;\n    }\n  }\n}\n","/* ----------------------------------------------------------------------------\n * Variables\n * ------------------------------------------------------------------------- */\n\n/* ----------------------------------------------------------------------------\n * Modular TypeScale\n *\n *\n * ------------------------------------------------------------------------- */\n$base--spacing: 1.8rem; //was 2.2rem\n$base--fontsize: 16px; //was 16\n$base--line: 24px; //was 24\n\n/* ----------------------------------------------------------------------------\n * Colors\n * ------------------------------------------------------------------------- */\n\n$c1: #dedede;\n$c2: #292929;\n\n$w: #fff;\n$b: #000;\n$gb: #686868;\n$g: #35ad68;\n$y: #fffdd2;\n\n// ==========================================================================\n// Global Config\n// ==========================================================================\n\n// Font Stacks\n$font-url--ubuntu: 'http://fonts.googleapis.com/css?family=Ubuntu:300,400,500,400italic';\n$ubuntu: 'Ubuntu', sans-serif;\n$courier: Courier, 'Lucida Sans Typewriter', 'Lucida Typewriter', monospace;\n$courier-new: 'Courier New', Courier, 'Lucida Sans Typewriter', 'Lucida Typewriter', monospace;\n$heading-font: $ubuntu;\n\n\n// Font Weights\n$font-weight--thin: 100;\n$font-weight--light: 300;\n$font-weight--medium: 400;\n$font-weight--bold: 700;\n$letter-space: 1px;\n\n// Descriptive Base Colors\n$white: #fff;\n$black: #222;\n$light-grey: #e7e7e7;\n$lighter-grey: #f3f3f3;\n$grey: #e8e8e8;\n$dark-grey: #d0d0d0;\n$darker-grey: #959595;\n$subtext-dk-grey: #6b6b6b;\n\n$shadow-m: rgba(0, 0, 0, .6);\n\n//Hover base colors\n$lighter-grey-hover: #d4d4d4;\n$dark-grey-hover: #474747;\n\n// BRAND COLOR\n$pink: #F9B0A3;\n$blue: #BCE3E0;\n$dark-blue: #313A54;\n$gold: #BFA66D;\n$dark-gold: #9c8758;\n\n$brown-grey: #7C7C7C;\n$light-brown:#928C85;\n\n$yellow: #F4DBA4;\n$yellow-hover: #e8c16a;\n$yellow-hover-text: #7b6b45;\n\n$cinn: #DE7157;\n$cinn-dark: #9B5D4F;\n$dark-red: #65362C;\n\n$purple: #B494EF;\n$lavendar: #c0bddd;\n$green: #6ED69A;\n\n$primary-color: $pink;\n$secondary-color: $dark-blue;\n$shadow-color: rgba($black, .2);\n\n//** Global text color on `<body>`.\n$text-color: #555 !default;\n\n//** Global textual link color.\n$link-color: $primary-color !default;\n\n//** Link hover color set via `darken()` function.\n$link-hover-color: darken($link-color, 15%) !default;\n\n//SCREEN SIZES\n$desktop:                 1201px !default; //1200px\n$laptop:                  992px !default; //992px\n$tablet:                  48em !default; //768px\n$mobile:                  34em !default; //544px\n\n// So media queries don't overlap when required, provide a maximum\n$mobile-max:              ($tablet - 1) !default;\n$tablet-max:              ($laptop - 1) !default;\n$laptop-max:              ($desktop - 1) !default;\n\n//Z-Index categories\n$below-content: 1;\n$content: 2;\n$above-content: 3;\n\n//Header Sizes\n$header-s: 60px;\n$header-l: 130px;\n","/* ----------------------------------------------------------------------------\n * Helpers\n * ------------------------------------------------------------------------- */\n\n.bold {\n  font-weight: 700;\n}\n\n.italic {\n  font-style: italic;\n}\n.text-center{\n  text-align: center;\n}\n.text-hide {\n  background-color: transparent;\n  border: 0;\n  color: transparent;\n  font: 0/0 a;\n  text-shadow: none;\n}\n\n.no-padding {\n  padding: 0 !important;\n}\n\n.no-margin {\n  margin: 0 !important;\n}\n\n.white-bg {\n  background: $white;\n}\n\n.black-bg {\n  background: $black;\n}\n\n.light-grey-bg {\n  background-color: $lighter-grey;\n}\n\n.grey-bg {\n  background-color: $grey;\n}\n\n.eltdf-btn.eltdf-btn-solid:not(.eltdf-btn-custom-hover-bg):hover{\n  color:$cinn !important;\n  background-color: $gold !important;\n}\n\n// btn dark blue\n.eltdf-btn.eltdf-btn-solid:not(.eltdf-btn-custom-hover-bg).et-btn-dk-blue:hover{\n  color:$white !important;\n  background-color: $dark-blue !important;\n}\n\n.eltdf-btn.eltdf-btn-solid:not(.eltdf-btn-custom-hover-bg).et-btn-dk-grey:hover{\n  color:$white !important;\n  background-color: $dark-grey-hover !important;\n}\n\n\n.et-license-list{\n\n  li{\n    list-style: none;\n    padding-bottom:20px;\n    position: relative;\n    padding-left: 20px;\n\n    &:before{\n      position: absolute;\n      top: 0;\n      left: 0;\n      content: \"\\f054\";\n      font-family: FontAwesome;\n      font-size: 14px;\n      color: $cinn;\n    }\n  }\n}\n\n.href-link:before {\n  display: block;\n  content: \" \";\n  margin-top: -215px;\n  height: 200px;\n  visibility: hidden;\n}\n\n.embed-responsive {\n  position: relative;\n  display: block;\n  height: 0;\n  padding: 0;\n  //overflow: hidden;\n  margin: 0 auto;\n  text-align: center;\n\n  .embed-responsive-item, iframe{\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    borderr: 0;\n  }\n}\n\n.embed-responsive-16by9{\n  padding-bottom: 56.25%;\n}\n\n.et-outer-padding{\n  padding: 120px 0;\n\n  @include e(top){\n    padding: 120px 0 0 0;\n  }\n\n  @include e(bottom){\n    padding: 0 0 120px 0;\n  }\n}\n\n.shadow-small-btn {\n  -webkit-box-shadow: 0px 20px 45px -6px rgba(0, 0, 0, 0.2);\n  -moz-box-shadow: 0px 20px 45px -6px rgba(0, 0, 0, 0.2);\n  box-shadow: 0px 20px 45px -6px rgba(0, 0, 0, 0.2);\n}\n.shadow-small {\n  -webkit-box-shadow: 0 15px 35px rgba(50,50,93,.1),0 5px 15px rgba(0,0,0,.07);\n  -moz-box-shadow: 0 15px 35px rgba(50,50,93,.1),0 5px 15px rgba(0,0,0,.07);\n  box-shadow: 0 15px 35px rgba(50,50,93,.1),0 5px 15px rgba(0,0,0,.07);\n}\n\n.inner-shadow-small{\n  .eltdf-section-inner-margin{\n    -webkit-box-shadow: 0px 20px 45px -6px rgba(0, 0, 0, 0.2);\n    -moz-box-shadow: 0px 20px 45px -6px rgba(0, 0, 0, 0.2);\n    box-shadow: 0px 20px 45px -6px rgba(0, 0, 0, 0.2);\n  }\n}\n\n.shadow-medium {\n  -webkit-box-shadow: 0px 15px 75px -6px rgba(0,0,0,0.38);\n  -moz-box-shadow: 0px 15px 75px -6px rgba(0,0,0,0.38);\n  box-shadow: 0px 15px 75px -6px rgba(0,0,0,0.38);\n}\n\n.divider-bottom {\n  border-bottom: 1px solid $light-grey;\n}\n.divider-bottom-top {\n  border-top: 1px solid $light-grey;\n  border-bottom: 1px solid $light-grey;\n}\n.divider-top {\n  border-top: 1px solid $light-grey;\n}\n\n.img-responsive{\n  display: block;\n  max-width: 100%;\n  height: auto;\n}\n\n.et-cat{\n  display: inline-block;\n  padding: 6px 15px;\n  border-radius: 25px;\n  margin: 0 auto;\n  text-align: center;\n  font-size: 11px;\n  line-height: 11px;\n  color: $white;\n  font-weight: 600;\n  text-transform: uppercase;\n  @include transition(all .3s);\n  cursor: pointer;\n\n  &.cat-red{\n    background-color: $cinn;\n\n    &:hover{\n      background-color: $blue;\n      color: $dark-blue;\n\n      a{\n        color: $dark-blue;\n      }\n\n    }\n\n  }\n\n  a{\n    color: $white;\n\n  }\n\n  .box-two & {\n    position: absolute;\n    top: 25px;\n    left: 25px;\n  }\n}\n.circle-dot{\n  position: relative;\n  border-radius: 50%;\n  width: 43px;\n  height: 43px;\n  margin-right: 15px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  i{\n    text-align: center;\n    font-size: 20px;\n  }\n\n  @include breakpoint(tablet){\n    width: 66px;\n    height: 66px;\n    margin-right: 20px;\n\n    i{\n      font-size: 36px;\n    }\n\n  }\n\n}\n\n\n// Global Seperator class\n.et2017-seperator{\n  overflow: hidden;\n}\n.et2017-seperator .eltdf-separator,\n.et-dotted-divider{\n    border: none;\n    border-right: 0;\n    border-left: 0;\n    // Safari & Chrome need border-style and border-color Â¯\\_(ã)_/Â¯\n    border-style: dotted;\n    border-image-source: url('./assets/images/dots.svg');\n    border-image-slice: 20% 20%;\n    border-image-repeat: space;\n    border-width: 4px 0 0 0;\n\n  // Home page Tabs - Feature Item\n  .et-latest-news .eltdf-pb-one-holder & {\n    border-width: 0 0 4px 0;\n  }\n\n  // Home page Tabs - small items\n  .et-latest-news .eltdf-post-item & {\n    border-width: 4px 0 0;\n  }\n\n  // Blog Index feature blog module\n  &.et2017-blog-feature-item{\n    border-width: 0 0 4px 0;\n  }\n\n  // Blog List Items on blog index page\n  &.eltdf-pt-three-item-inner{\n    border-width: 0 0 4px 0;\n  }\n\n  // Primary Navigation\n  .eltdf-header-type3 .eltdf-menu-area &{\n    border-width: 4px 0 0 0;\n  }\n\n\n}\n\n.fix-line-height{\n  @include e(h3){\n    h3{\n      font-size: 21px !important;\n      line-height: 36px !important;\n    }\n  }\n\n  //used on about page - mission module\n  @include e(large){\n    h3{\n      @include breakpoint(rd-tablet-max){\n        font-size: 33px !important;\n        line-height: 42px !important;\n      }\n    }\n  }\n}\n\n.et-round-borders{\n\n  .eltdf-section-inner-margin{\n    border-radius: 10px;\n    overflow: hidden;\n  }\n}\n\n.dots-bg-top{\n  background-repeat: repeat-x !important;\n}\n","@charset \"UTF-8\";\n\n/// A mixin for generating vendor prefixes on non-standardized properties.\n///\n/// @param {String} $property\n///   Property to prefix\n///\n/// @param {*} $value\n///   Value to use\n///\n/// @param {List} $prefixes\n///   Prefixes to define\n///\n/// @example scss - Usage\n///   .element {\n///     @include prefixer(border-radius, 10px, webkit ms spec);\n///   }\n///\n/// @example css - CSS Output\n///   .element {\n///     -webkit-border-radius: 10px;\n///     -moz-border-radius: 10px;\n///     border-radius: 10px;\n///   }\n///\n/// @require {variable} $prefix-for-webkit\n/// @require {variable} $prefix-for-mozilla\n/// @require {variable} $prefix-for-microsoft\n/// @require {variable} $prefix-for-opera\n/// @require {variable} $prefix-for-spec\n\n@mixin prefixer($property, $value, $prefixes) {\n  @each $prefix in $prefixes {\n    @if $prefix == webkit {\n      @if $prefix-for-webkit {\n        -webkit-#{$property}: $value;\n      }\n    } @else if $prefix == moz {\n      @if $prefix-for-mozilla {\n        -moz-#{$property}: $value;\n      }\n    } @else if $prefix == ms {\n      @if $prefix-for-microsoft {\n        -ms-#{$property}: $value;\n      }\n    } @else if $prefix == o {\n      @if $prefix-for-opera {\n        -o-#{$property}: $value;\n      }\n    } @else if $prefix == spec {\n      @if $prefix-for-spec {\n        #{$property}: $value;\n      }\n    } @else  {\n      @warn \"Unrecognized prefix: #{$prefix}\";\n    }\n  }\n}\n\n@mixin disable-prefix-for-all() {\n  $prefix-for-webkit:    false !global;\n  $prefix-for-mozilla:   false !global;\n  $prefix-for-microsoft: false !global;\n  $prefix-for-opera:     false !global;\n  $prefix-for-spec:      false !global;\n}\n","/* ----------------------------------------------------------------------------\n * Structure\n * ------------------------------------------------------------------------- */\n$line-height-mobile: 2.4rem;\n$line-height-desktop: 2rem;\n$half-line-height: #{$line-height-mobile / 2rem}rem;\nhtml{\n  position: relative;\n}\nbody{\n  position: inherit;\n  display: block;\n  z-index: 544;\n  font-size: 16px;\n\n  @include breakpoint(tablet){\n    z-index: 768;\n  }\n  @include breakpoint(laptop){\n    z-index: 992;\n  }\n  @include breakpoint(desktop){\n    z-index: 1200;\n  }\n  @include breakpoint(desktopXL){\n    z-index: 1600;\n  }\n}\n\n// Added zindex control to help with CK pop-ups\n.eltdf-wrapper,\n.eltdf-content,\n.eltdf-container,\n.eltdf-grid-section .eltdf-section-inner,\n.eltdf-full-width,\n.eltdf-section{\n  position: initial;\n}\n.eltdf-grid-section .eltdf-section-inner{\n}\n.insta-modal__open{\n  position: fixed;\n  bottom:0;\n}\n\n.insta-modal__nav-active{\n  -webkit-transform: translateY(0) !important;\n  transform: translateY(0) !important;\n}\n\n// fix for nav on safari\n.eltdf-page-header .eltdf-sticky-header.header-appear{\n  @include transform(translateY(0));\n}\n\np{\n  color: $text-color;\n\n  a{\n    color: $cinn;\n    font-weight: 500;\n  }\n}\n\n//Category page list\n.category,\n.search{\n\n  .et2017-bnl-holder{\n    .eltdf-bnl-inner{\n      display: flex;\n      flex-direction: row;\n      flex-wrap: wrap;\n      flex:1 0 100%;\n\n    }\n    .et-post-item__inner{\n      padding: 0 !important;\n      margin: 0 12px;\n      position: relative;\n      flex: 1;\n    }\n    .eltdf-pt-one-item{\n\n      & > div{\n        padding-left: 12px;\n        padding-right: 12px;\n        display: block;\n      }\n      .eltdf-pt-one-image-holder{\n      }\n      .eltdf-pt-one-content-holder{\n        display: flex;\n        padding-bottom: 39px;\n        flex-direction: column;\n        width: auto;\n      }\n      .eltdf-pt-info-section{\n        position: absolute;\n        bottom:0;\n      }\n\n      &:hover{\n        .eltdf-pt-one-title-holder .eltdf-pt-one-title{\n          a{\n            color:$light-brown;\n          }\n        }\n      }\n    }\n  }\n\n  .et2017-bnl-holder .eltdf-bnl-outer .eltdf-bnl-inner>.eltdf-post-item{\n    display: flex;\n    flex-direction: column;\n    flex: 1 0 33%;\n    float: none;\n    box-sizing: inherit;\n    padding: 0;\n    max-width: 408px;\n\n    @include breakpoint(rd-laptop-max){\n      flex: 1 0 50%;\n    }\n\n    @include breakpoint(rd-tablet-max){\n      flex: 1 0 50%;\n    }\n\n    @include breakpoint(rd-mobile-h-max){\n      flex: 1 0 100%;\n    }\n\n  }\n\n}\n\n//Related Posts\n.eltdf-related-posts-holder .eltdf-related-posts-inner{\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n}\n\n.eltdf-content-left-from-sidebar .eltdf-related-posts-holder .eltdf-related-posts-inner .eltdf-related-post{\n  float: none;\n  width: auto;\n\n  display: flex;\n  flex-direction: column;\n  flex: 1 0 50%;\n\n  @include breakpoint(rd-mobile-h-max){\n    flex: 1 0 100%;\n  }\n}\n\n//Added opactiy transition for pinit animation\na{\n  img{\n    @include transition(opacity .3s);\n  }\n}\n\n//back to top\n#eltdf-back-to-top>span i{\n  position: absolute;\n  top: 40%;\n  @include transform(translateY(-50%));\n}\n\n//Related blog posts\n.eltdf-related-posts-holder .eltdf-related-content .eltdf-related-title{\n  min-height: 58px;\n}\n\na:hover,\nh1 a:hover,\nh2 a:hover,\nh3 a:hover,\nh4 a:hover,\nh5 a:hover,\nh6 a:hover,\np a:hover,\n.eltdf-pt-six-item.eltdf-item-hovered .eltdf-pt-six-title a,\n.eltdf-pt-three-item.eltdf-item-hovered .eltdf-pt-three-title,\n.eltdf-related-posts-holder .eltdf-related-posts-inner .eltdf-related-info-section>div>div a:hover,\n.eltdf-related-post:hover .eltdf-related-content .eltdf-related-title a,\n.eltdf-pt-one-item.eltdf-item-hovered .eltdf-pt-one-title a,\n.eltdf-pt-three-item.eltdf-item-hovered .eltdf-pt-three-title a{\n  color: $gold;\n}\n\nbutton{\n  &:active, &:focus{\n    outline: none;\n  }\n}\n","//When editing this make sure to check the products page that it doesnt get jacked up!\n.et-rd-container{\n\n  position: relative;\n  width:1200px;\n  margin: 0 auto;\n\n  @include breakpoint(rd-desktopXL){\n    width: 1200px;\n  }\n  @include breakpoint(rd-desktop-max){\n    width: 950px;\n  }\n  @include breakpoint(rd-laptop-max){\n    width: 768px;\n  }\n  @include breakpoint(rd-tablet-max){\n    width: 600px;\n  }\n  @include breakpoint(rd-mobile-h-max){\n    width: 420px;\n  }\n  @include breakpoint(rd-mobile-v-max){\n    width: 300px;\n  }\n\n}\n\n.et-container-full-width{\n  position: relative;\n  max-width:1200px;\n  margin: 0 auto;\n}\n\n.et-container-inner{\n  position: relative;\n  margin: 0 auto;\n  @include breakpoint(tablet){\n      width: 750px;\n  }\n  @include breakpoint(laptop){\n      width: 768px;\n  }\n  @include breakpoint(desktop){\n    width: 1170px;\n  }\n}\n\n.flex-container {\n  display: flex;\n  flex-direction: column;\n  margin: 0 5%;\n\n  @include breakpoint(tablet) {\n    flex-direction: row;\n    margin: 0 2%;\n  };\n\n  &.list{\n\n\n\n    @include breakpoint(tablet) {\n      margin: 0 2%;\n      flex-direction: column;\n    };\n\n    @include breakpoint(laptop) {\n      flex-direction: row;\n      min-height: 555px;\n    };\n  }\n\n  &.bg-image-container{\n    @include breakpoint(laptop) {\n      min-height: 700px;\n    };\n  }\n\n  &.reverse{\n    @include breakpoint(tablet) {\n      flex-direction: row-reverse;;\n    };\n  }\n\n  &.bonus-container{\n    flex-wrap: wrap;\n  }\n\n}\n\n.flex-row {\n  display: flex;\n  //display: -webkit-flex;\n  flex-direction: column;\n}\n\n.flex-row-md {\n  @include breakpoint(tablet){\n    flex-direction: row;\n    flex-wrap: wrap;\n    //-webkit-flex-wrap: wrap;\n  }\n}\n\n.flex-xs {\n  position: relative;\n  flex: 1 0;\n  max-width: 100%;\n  min-height: 1px;\n  display: flex;\n}\n\n.flex-sm-4{\n  @include breakpoint(tablet){\n    flex: 1 0 50%;\n    max-width: 50%;\n    //-webkit-flex: 1 0 50%;\n  }\n}\n\n.flex-md-6{\n  @include breakpoint(laptop-rd){\n    flex: 1 0 33.333333%;\n    //-webkit-flex: 1 0 33.333333%;\n  }\n}\n\n\n//New correct numbering\n.et-flex-sm-4{\n  flex: 1 0 33.333333%;\n}\n\n.et-flex-sm-6{\n  flex: 1 0 50%;\n}\n\n.et-flex-md-3{\n  @include breakpoint(laptop-rd){\n    flex: 1 0 25%;\n  }\n}\n.et-flex-md-4{\n  @include breakpoint(laptop-rd){\n    flex: 1 0 33.333333%;\n  }\n}\n.et-flex-md-7{\n  @include breakpoint(laptop-rd){\n    flex: 1 0 58.333333%;\n  }\n}\n.et-flex-md-8{\n  @include breakpoint(laptop){\n    flex: 1 0 66.666667%;\n  }\n}\n\n.equal-col-height{\n  .eltdf-section-inner-margin{\n    display: flex;\n    flex-direction: column;\n\n    @include breakpoint(laptop){\n        flex-direction: row;\n    }\n  }\n\n  .vc_column_container{\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n\n    .vc_column-inner{\n      flex: 1;\n    }\n  }\n}\n\n.et-flex-vc{\n\n  .eltdf-full-section-inner,\n  .eltdf-section-inner-margin{\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    margin: 0;\n    @include breakpoint(laptop){\n      flex-direction: row;\n    }\n  }\n\n  .vc_col-md-8,\n  .vc_col-md-7{\n    flex: 1 0 100%;\n    @include breakpoint(laptop){\n        flex: 1 0 66.66666667%;\n    }\n    .vc_column-inner{\n      height:100%;\n      min-height: 400px;\n    }\n  }\n\n  .vc_col-md-4,\n  .vc_col-md-5{\n    flex: 1 0 100%;\n\n    .vc_column-inner{\n      padding: 15%;\n\n      @include breakpoint(tablet){\n          padding: 10% 15%;\n      }\n\n      @include breakpoint(laptop){\n          padding: 15%;\n      }\n    }\n\n    @include breakpoint(laptop){\n        flex: 1 0 33.33333333%;\n    }\n  }\n\n  .vc_col-md-6{\n    flex: 1 0 100%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n\n    @include breakpoint(laptop){\n      flex: 1 0 50%;\n    }\n\n    @include first(1){\n      .vc_column-inner{\n        flex:1;\n        height:100%;\n        min-height: 400px;\n      }\n    }\n\n    @include last(1){\n      .vc_column-inner{\n        padding: 15%;\n\n        @include breakpoint(tablet){\n          padding: 10% 15%;\n        }\n\n        @include breakpoint(laptop){\n          padding: 15%;\n        }\n      }\n    }\n  }\n\n}",".et-btn{\n  padding: 1px 25px;\n  cursor: pointer;\n  background-color: $pink !important;\n  min-width: 158px;\n  font-size: .8em;\n  line-height: 3.5em;\n  @include transition(color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out);\n\n  &:after{\n    content: '';\n    width: 29px;\n    height: 28px;\n    background-color: $pink;\n    position: absolute;\n    top: 7px;\n    right: -15px;\n    display: block;\n    @include transform(rotate(45deg));\n    @include transition(color .15s ease-in-out, background-color .15s ease-in-out,border-color .15s ease-in-out);\n  }\n\n  &:hover {\n    color: $white;\n    background-color: $dark-blue !important;\n    &:after {\n      background-color: $dark-blue;\n    }\n  }\n}\n\n.et-btn-round,\n.et-btn-round-vc{\n  border: 3px solid $dark-blue;\n  border-radius: 50px;\n  font-size: 16px;\n  line-height: 16px;\n  text-transform: uppercase;\n  font-weight: 600;\n  padding: 15px 40px;\n  max-width: 125px;\n  cursor: pointer;\n  @include transition(color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out);\n\n  &:hover{\n    color:$white;\n    background: $dark-blue;\n  }\n\n  &.eltdf-btn.eltdf-btn-outline{\n    border: 3px solid $dark-blue;\n    color: $dark-blue;\n    margin: 0 auto;\n    line-height: 16px;\n    letter-spacing: 0;\n    max-width: 250px;\n    font-weight: bold;\n    padding: 16px 33px;\n    display: inline-block;\n    text-align: center;\n\n    &:hover{\n      border-color: $dark-blue !important;\n      color:$white;\n      background: $dark-blue !important;\n    }\n\n    @include breakpoint(tablet){\n      max-width: 350px;\n    }\n    @include breakpoint(laptop){\n      max-width: 250px;\n    }\n\n    .wpb_wrapper &{\n      text-align: center;\n    }\n  }\n}\n\n.et-btn-left {\n  margin: 0 !important;\n}\n\n.et-outline__black{\n  color: #000 !important;\n  border: 3px solid #000 !important;\n  &:hover{\n    color: $pink !important;\n    border: 3px solid $pink !important;\n    background: #fff !important;\n  }\n}\n\n.et-btn-large {\n  font-size: .9em;\n\n  &:after{\n    width: 32px;\n    height: 32px;\n    position: absolute;\n    top: 7px;\n    right: -16px;\n  }\n}\n\n.et-btn-blue {\n  background-color: $blue !important;\n  color: $dark-blue !important;\n\n  &:after{\n    background-color: $blue;\n  }\n\n  &:hover {\n    &:after {\n      color: $cinn;\n      background-color: $gold;\n    }\n  }\n}\n\n.et-btn-dk-blue {\n  background-color: transparent;\n  color: $dark-blue;\n\n  &:after{\n    background-color: #5D6F80;\n  }\n\n  &:hover {\n\n    background-color: $dark-blue;\n    border-color: $dark-blue;\n\n    &:after {\n      background-color: $dark-blue;\n    }\n  }\n}\n\n.et-btn-pink {\n  background-color: $pink !important;\n  color: $white !important;\n\n  &:after{\n    background-color: $pink;\n  }\n\n  &:hover {\n    &:after {\n      background-color: $gold;\n    }\n  }\n}\n\n.et-btn-dk-grey {\n  background-color: transparent;\n  color: $dark-blue;\n\n  &:after{\n    background-color: $brown-grey;\n  }\n\n  &:hover {\n    color: $white;\n    background: $brown-grey;\n    border-color:$brown-grey;\n\n    &:after {\n      background-color: $dark-grey-hover;\n    }\n  }\n}\n\n.et-btn-dk-red {\n  background-color: transparent;\n  color: $dark-blue;\n\n  &:after{\n    background-color: $cinn-dark;\n  }\n\n  &:hover {\n\n    background-color: $dark-red;\n    border-color: $dark-red;\n\n    &:after {\n      background-color: $dark-red;\n    }\n  }\n}\n\n.et2017-more-link{\n\n  .eltdf-btn.eltdf-btn-solid{\n    color: $dark-blue;\n    background-color: transparent;\n    border: 3px solid $dark-blue;\n    border-radius: 25px;\n    margin: 5px 0 30px;\n\n    &:hover{\n      background-color: $dark-blue !important;\n      border-color: $dark-blue !important;\n      color: $white !important;\n    }\n\n  }\n\n}\n\n.et-close{\n  overflow: hidden;\n  background-color: transparent;\n  position: absolute;\n  right: 20px;\n  text-indent: 100%;\n  top: 15px;\n  height: 30px;\n  width: 30px;\n  z-index: 3;\n  border: 2px solid #c4c7d0;\n  border-radius: 50%;\n  cursor: pointer;\n\n\n  &:after, &:before {\n    background-color: #222;\n    content: '';\n    height: 20px;\n    left: 50%;\n    position: absolute;\n    top: 50%;\n    width: 2px;\n  }\n\n  &:before{\n    @include transform(translateX(-50%)translateY(-50%)rotate(-45deg))\n  }\n  &:after{\n    @include transform(translateX(-50%)translateY(-50%)rotate(45deg))\n  }\n}\n\n.eltdf-btn.eltdf-btn-solid:not(.eltdf-btn-custom-hover-bg).et-btn-dk-red:hover{\n  color:$white !important;\n  background-color: $dark-red !important;\n}\n\n.pill-btn{\n  padding: 7px 10px;\n  background: $pink;\n  border-radius: 25px;\n  margin-left: 5px;\n  color: $white;\n  text-transform: uppercase;\n  font-weight: 600;\n  font-size: 13px;\n  @include transition(all .3s);\n  cursor: pointer;\n\n  &:hover{\n    color: $white;\n    background-color: $cinn-dark;\n  }\n}","// Adds keyframes blocks for supported prefixes, removing redundant prefixes in the block's content\n@mixin keyframes($name) {\n  $original-prefix-for-webkit:    $prefix-for-webkit;\n  $original-prefix-for-mozilla:   $prefix-for-mozilla;\n  $original-prefix-for-microsoft: $prefix-for-microsoft;\n  $original-prefix-for-opera:     $prefix-for-opera;\n  $original-prefix-for-spec:      $prefix-for-spec;\n\n  @if $original-prefix-for-webkit {\n    @include disable-prefix-for-all();\n    $prefix-for-webkit: true !global;\n    @-webkit-keyframes #{$name} {\n      @content;\n    }\n  }\n\n  @if $original-prefix-for-mozilla {\n    @include disable-prefix-for-all();\n    $prefix-for-mozilla: true !global;\n    @-moz-keyframes #{$name} {\n      @content;\n    }\n  }\n\n  $prefix-for-webkit:    $original-prefix-for-webkit    !global;\n  $prefix-for-mozilla:   $original-prefix-for-mozilla   !global;\n  $prefix-for-microsoft: $original-prefix-for-microsoft !global;\n  $prefix-for-opera:     $original-prefix-for-opera     !global;\n  $prefix-for-spec:      $original-prefix-for-spec      !global;\n\n  @if $original-prefix-for-spec {\n    @keyframes #{$name} {\n      @content;\n    }\n  }\n}\n","@include keyframes(etp-fade-in) {\n  0% {\n    opacity: 0;\n    visibility: hidden;\n  }\n\n  100% {\n    opacity: 1;\n    visibility: visible;\n  }\n}\n/*------------------------------------*\\\n    Mobile header\n\\*------------------------------------*/\n.eltdf-mobile-header{\n  z-index: 1000;\n}\n.eltdf-mobile-header .eltdf-mobile-nav ul{\n  padding: 20px;\n}\n.eltdf-mobile-header .eltdf-mobile-nav a{\n  padding: 15px 0;\n}\n\n/*------------------------------------*\\\n    Icon Nav\n\\*------------------------------------*/\n.et-icon-nav{\n  img{\n    width: 175px;\n  }\n}\n.blue-background {\n  background-color: $blue;\n}\n\n/*------------------------------------*\\\n    Search\n\\*------------------------------------*/\n.et2017_nav_search_widget{\n  position: relative;\n  display: inline-block;\n  vertical-align: middle;\n  margin: 0 0 0 10px;\n  padding: 0;\n  border-left: 1px solid $grey;\n}\n\n.et2017-navsearch{\n  position: relative;\n\n  @include e(btn){\n    padding: 0 20px;\n    cursor: pointer;\n    position: relative;\n    overflow: hidden;\n\n    i{\n      color: $black;\n    }\n\n    .fa-times{\n      color: $cinn;\n      position: absolute;\n      top:-20px;\n      left:50%;\n      @include transform(translateY(0)translateX(-50%));\n      @include transition(all .3s);\n    }\n    .fa-search{\n      position: relative;\n      top: 0;\n      @include transition(all .3s);\n    }\n\n    &.active{\n      .fa-times{\n        top:50%;\n        left:50%;\n        @include transform(translateY(-50%)translateX(-50%));\n      }\n      .fa-search{\n        top: 25px;\n      }\n    }\n  }\n\n  @include e(bar){\n    position: absolute;\n    @include transform(translate3d(0,-32px,0) scale(0) );\n    right: 8px;\n    display: none;\n    background-color: $white;\n    border: 2px solid $blue;\n    width: 35px;\n    height: 35px;\n    border-radius: 25px;\n    @include transform-origin(center);\n\n    form, div{\n      display: block;\n      width: 200px;\n      height: 35px;\n      position: relative;\n      margin: 0;\n      padding: 0;\n    }\n  }\n\n  input[type=text]{\n    height: 35px;\n    width: 185px;\n    border: none;\n    background-color: $white;\n    color: $dark-blue;\n    font-size: 14px;\n    opacity: 0;\n    margin: 0;\n    padding: 0 15px;\n    @include transition(box-shadow .3s);\n    border-radius: 25px; /* top left, top right, bottom right, bottom left */\n    &:focus{\n      outline: none;\n    }\n  }\n\n\n\n  .eltdf-search-widget-icon{\n    display: none;\n  }\n}\n\n//weird bug - when we echo the get_search_form it appears in two plcaes - so we are hiding the first one\n.header-search{\n  //display: none;\n}\n\n/*------------------------------------*\\\n    Online Courses update btn\n\\*------------------------------------*/\n.eltdf-page-header .eltdf-logo-area{\n  z-index: 1000;\n  overflow: visible; //can toggle if needed on click\n}\n#et2017-notify{\n\n  ul{\n    position: absolute;\n    width: 90%;\n    max-width: 245px;\n    right: 5px;\n    top: 40px;\n    overflow: hidden;\n    border-radius: 50%;\n    box-shadow: 0 0 0px rgba($dark-grey, 0);\n    background: $white;\n    z-index: 1;\n    will-change: transform;\n\n    /* Force Hardware Acceleration in WebKit */\n    -webkit-backface-visibility: hidden;\n    backface-visibility: hidden;\n\n    @include transform(scale(0));\n    @include transition(transform .3s, visibility 0s, box-shadow 0s, border-radius .3s);\n    @include transform-origin(100% 0%);\n\n    li {\n      /* Force Hardware Acceleration in WebKit */\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n      list-style: none;\n      border-bottom: 1px solid $grey;\n      padding: 0;\n      display: flex;\n      opacity: 0;\n      @include transform(translateX(100px));\n      @include transition(background .3s);\n\n      a, span{\n        padding: 15px;\n        width: 100%;\n      }\n\n      &:hover{\n\n        background: $grey;\n\n        a{\n          color: $dark-blue;\n        }\n      }\n\n      &:nth-child(1) { /* list items animation */\n        padding-top: 5px;\n        padding-bottom: 5px;\n        border-radius: 5px 25px 5px 5px;\n        width: 80%;\n      }\n\n      @for $i from 1 through 9 {\n        &:nth-child(#{$i}) {\n          @include transition(opacity .1s, transform 0.3s 0.035s * $i, background .3s);\n\n        }\n      }\n\n    }\n\n    .notify-title{\n      a, span{\n        padding: 10px 15px;\n      }\n      font-size: 13px;\n      line-height: 13px;\n      color: $dark-blue;\n      &:hover{\n        background-color: transparent;\n      }\n    }\n    .notify-link,\n    .notify-link-lg{\n      position: relative;\n\n\n      a{\n        position: relative;\n\n        .fa-chevron-right{\n          font-size: 12px;\n          font-weight: 300;\n          color: lighten($dark-blue, 30) !important;\n          position: absolute;\n          @include transform(translateY(-50%));\n          top: 50%;\n          right: 17px;\n          @include transition(color .3s);\n        }\n\n      }\n\n      &:hover{\n\n        background: $grey;\n\n        a{\n          color: $dark-blue;\n        }\n      }\n    }\n\n    .notify-login{\n      //background-color: $blue;\n\n      &:hover{\n        //color: $white;\n        //background-color: #639CBF;\n        .fa-chevron-right{\n          color: $dark-blue !important;\n        }\n      }\n    }\n\n    .notify-item-lg{\n      font-size: 15px;\n      font-weight: 500;\n      line-height: 18px;\n      min-height: 67px;\n\n      a{\n        padding-left: 50px;\n        padding-right: 42px;\n        align-self: center;\n      }\n\n      i:not(.fa-chevron-right){\n        position: absolute;\n        font-size: 24px;\n        color: $dark-blue;\n        left: 16px;\n        top: 49%;\n        @include transform(translateY(-50%));\n      }\n\n      &.nav-item-orange{\n        i{\n          color: #f90;\n        }\n      }\n      &.nav-item-purple{\n        i{\n          color: #9e3f93;\n        }\n      }\n    }\n\n    &.is-visible{\n      @include transform(scale(1));\n      visibility: visible;\n      border-radius: 5px 25px 5px 5px;\n      box-shadow: 0 0 10px rgba($black, .4);\n      li{\n        @include transform(translateX(0));\n        opacity: 1;\n      }\n    }\n  }\n}\n\n.et2017-notify{\n\n  @include e(container){\n\n    transition-timing-function: ease-in;\n    animation-fill-mode: backwards;\n    animation-duration: .5s;\n    animation-delay: 1.3s;\n    animation-name: etp-fade-in;\n\n\n    &:before{\n      content: '';\n      position: fixed;\n      z-index: 1;\n      height: 100vh;\n      width: 100vw;\n      top: 0;\n      left: 0;\n      background: rgba(0, 0, 0, 0.5);\n      opacity: 0;\n      visibility: hidden;\n      -webkit-transition: opacity .2s, visibility .2s;\n      transition: opacity .2s, visibility .2s;\n    }\n    &.et2017-notify--open{\n\n      &:before{\n        opacity: 1;\n        visibility: visible;\n      }\n\n    }\n  }\n\n  @include e(trigger){\n    position: absolute;\n    top: 40px;\n    right: 5px;\n    width: 44px;\n    height: 44px;\n    background-color: $blue;\n    border-radius: 50%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n\n    @include transition(background .2s, box-shadow .3s);\n\n\n    /* image replacement */\n    white-space: nowrap;\n    z-index: 2;\n\n    &:hover{\n      //box-shadow: 0 6px 40px rgba(0, 0, 0, 0.3);\n      .title{\n        color: $dark-blue;\n      }\n      .et2017-notify--open & {\n        box-shadow: 0 0 0 rgba(0, 0, 0, 0) ;\n      }\n      i{\n        color: $dark-blue;\n      }\n    }\n\n    .title{\n      position: absolute;\n      left: -100px;\n      font-size: 13px;\n      opacity: 1;\n      @include transform(rotate(0deg));\n      @include transition( opacity .2s, transform .3s .05s, visibility 0s );\n      will-change: transform;\n      @include transform-origin(center right);\n      width: 123px;\n      visibility: visible;\n      z-index: 1;\n    }\n\n    .et2017-notify--open & {\n      box-shadow: 0 0 0 rgba(0, 0, 0, 0) ;\n      background-color: $white;\n      height: 43px;\n\n      .title{\n        opacity: 0;\n        @include transform(rotate(-45deg));\n        visibility: hidden;\n      }\n\n      .fa-times{\n        opacity: 1;\n        @include transform(translateY(-50%) translateX(-50%) rotate(-180deg));\n      }\n\n      .fa-play,\n      .fa-graduation-cap{\n        @include transform(rotate(-180deg));\n        @include transition(transform .3s, opacity .1s);\n        opacity: 0;\n      }\n    }\n\n    i{\n      position: relative;\n      font-size: 18px;\n      color: $white;\n    }\n\n    .fa-play,\n    .fa-graduation-cap{\n      @include transform(rotate(0deg));\n      @include transition(color .3s, transform .3s, opacity .1s);\n      opacity: 1;\n    }\n\n    .fa-times{\n      position: absolute;\n      color: $dark-blue;\n      left: 50%;\n      top: 50%;\n      @include transform(translateY(-50%) translateX(-50%) rotate(0deg));\n      @include transition(transform .3s, opacity .1s);\n      opacity: 0;\n    }\n\n  }\n\n  @include e(count){\n    background-color: $cinn;\n    width: 17px;\n    height: 17px;\n    border-radius: 50%;\n    position: absolute;\n    top: -7px;\n    right: -3px;\n    color: $white;\n    font-size: 13px;\n    text-align: center;\n    line-height: 16px;\n    padding: 2px;\n    font-weight: 800;\n    font-family: Arial, \"Helvetica Neue\", Helvetica, sans-serif;\n    @include transform(scale(1));\n    @include transition(transform .3s);\n    @include transform-origin(center left);\n\n    .et2017-notify--open & {\n      @include transform(scale(0));\n    }\n\n    span{\n      position: absolute;\n      top:50%;\n      left:53%;\n      @include transform(translateY(-50%)translateX(-50%));\n    }\n  }\n}\n\n@include keyframes(cd-slide-in) {\n  0% {\n    @include transform(translateX(100px));\n  }\n\n  100% {\n    @include transform(translateY(0));\n  }\n}\n\n\n\n/*------------------------------------*\\\n    Navigation\n\\*------------------------------------*/\n.eltdf-grid {\n  position: relative;\n}\n\n//nav arrows with fontawesome\n.eltdf-plw-tabs .eltdf-plw-tabs-tabs-holder .eltdf-plw-tabs-tab a .item_text:after{\n  top: 41%;\n  font-family: FontAwesome;\n  content:'\\f105';\n  font-size: 16px;\n}\n//nav arrows hover with fontawesome\n.eltdf-plw-tabs .eltdf-plw-tabs-tabs-holder .eltdf-plw-tabs-tab a:hover .item_text:after{\n  left: 3px;\n}\n\n//nav arrows hover with fontawesome on single menu items\n.eltdf-drop-down .eltdf-menu-second .eltdf-menu-inner>ul li>a .item_text:after{\n  top: 41%;\n  font-family: FontAwesome;\n  content:'\\f105';\n  font-size: 16px;\n}\n.eltdf-drop-down .eltdf-menu-second .eltdf-menu-inner>ul li>a:hover .item_text:after{\n  left: 4px;\n}\n\n//logo master container\n.eltdf-page-header .eltdf-grid .logo-primary-container{\n  padding-top: 20px;\n}\n\n// logo container height\n.eltdf-page-header .eltdf-logo-area {\n  height: 122px;\n}\n\n// logo height\n.eltdf-page-header .eltdf-logo-area .eltdf-logo-wrapper a{\n  height: 85px;\n}\n\n//nav dropdown default background\n.eltdf-drop-down .eltdf-menu-second .eltdf-menu-inner>ul, li.eltdf-menu-narrow .eltdf-menu-second .eltdf-menu-inner ul{\n  background-color: $dark-blue;\n}\n\n//nav dropdown default\n.eltdf-drop-down .eltdf-menu-second .eltdf-menu-inner ul li>a:hover{\n  color: $blue !important;\n}\n\n//meta info color on dropdown tabs\n.eltdf-plw-tabs {\n  .eltdf-post-item .eltdf-pt-info-section>div>div{\n    color: $white;\n  }\n}\n\n.site-description {\n  display: inline-block;\n  font-size: 12px;\n  line-height: 18px;\n  position: absolute;\n  margin-left: 32px;\n  top: 50%;\n  @include transform(translateY(-50%));\n}\n\n.eltdf-page-header{\n  z-index:1000;\n}\n\n//Class and products boxed nav fix\n.eltdf-menu-second{\n  h6{\n    margin-bottom: 20px;\n\n    &.et-cat{\n      cursor: default;\n      &:hover{\n        color: $white;\n        background-color: $cinn;\n      }\n    }\n\n  }\n\n\n\n  .eltdf-pt-one-item.eltdf-post-item.eltdf-active-post-page{\n    cursor: pointer;\n  }\n}\n\n//empty box nav dropdown\n.et2107-viewall-arrow{\n  width: 45px;\n  height: 3px;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  background-color: $white;\n  @include transform(translateY(-50%)translateX(-50%));\n  @include transition(all .3s);\n\n  &::before, &::after {\n    content: '';\n    height: 3px;\n    width: 20px;\n    position: absolute;\n    background-color: $white;\n    right: -4px;\n    @include transition(all .3s);\n  }\n\n  &::before{\n    top:-6px;\n    @include transform(rotate(45deg));\n  }\n\n  &::after{\n    bottom:-6px;\n    @include transform(rotate(-45deg));\n  }\n\n}\n\n.eltdf-bnl-holder .eltdf-bnl-outer .eltdf-bnl-inner .eltdf-post-item.eltdf-active-post-page.et2017-post-item-outline{\n\n  .eltdf-pt-one-image-holder{\n    position: relative;\n    width: 265px;\n    border: solid $white 3px;\n    height: 164px;\n    @include transition(border .3s);\n  }\n  &:hover{\n    .eltdf-pt-one-image-holder{\n      border: solid $gold 3px;\n    }\n\n    .et2107-viewall-arrow{\n      background-color: $gold;\n      &::before, &::after {\n        background-color: $gold;\n      }\n    }\n\n  }\n}\n\n.et2017-post-item-outline .eltdf-pt-one-image-holder .eltdf-pt-one-image-inner-holder{\n  height: 100%;\n}\n\n.eltdf-bnl-holder .eltdf-bnl-outer .et2017-post-item-outline {\n  .eltdf-image-link {\n    width: 100%;\n    height: 100%;\n    position: relative;\n  }\n}\n\n\n","// Shorthand mixin. Supports multiple parentheses-deliminated values for each variable.\n// Example: @include transition (all 2s ease-in-out);\n//          @include transition (opacity 1s ease-in 2s, width 2s ease-out);\n//          @include transition-property (transform, opacity);\n\n@mixin transition($properties...) {\n  // Fix for vendor-prefix transform property\n  $needs-prefixes: false;\n  $webkit: ();\n  $moz: ();\n  $spec: ();\n\n  // Create lists for vendor-prefixed transform\n  @each $list in $properties {\n    @if nth($list, 1) == \"transform\" {\n      $needs-prefixes: true;\n      $list1: -webkit-transform;\n      $list2: -moz-transform;\n      $list3: ();\n\n      @each $var in $list {\n        $list3: join($list3, $var);\n\n        @if $var != \"transform\" {\n          $list1: join($list1, $var);\n          $list2: join($list2, $var);\n        }\n      }\n\n      $webkit: append($webkit, $list1);\n      $moz:    append($moz,    $list2);\n      $spec:   append($spec,   $list3);\n    } @else {\n      $webkit: append($webkit, $list, comma);\n      $moz:    append($moz,    $list, comma);\n      $spec:   append($spec,   $list, comma);\n    }\n  }\n\n  @if $needs-prefixes {\n    -webkit-transition: $webkit;\n       -moz-transition: $moz;\n            transition: $spec;\n  } @else {\n    @if length($properties) >= 1 {\n      @include prefixer(transition, $properties, webkit moz spec);\n    } @else {\n      $properties: all 0.15s ease-out 0s;\n      @include prefixer(transition, $properties, webkit moz spec);\n    }\n  }\n}\n\n@mixin transition-property($properties...) {\n  -webkit-transition-property: transition-property-names($properties, \"webkit\");\n     -moz-transition-property: transition-property-names($properties, \"moz\");\n          transition-property: transition-property-names($properties, false);\n}\n\n@mixin transition-duration($times...) {\n  @include prefixer(transition-duration, $times, webkit moz spec);\n}\n\n@mixin transition-timing-function($motions...) {\n  // ease | linear | ease-in | ease-out | ease-in-out | cubic-bezier()\n  @include prefixer(transition-timing-function, $motions, webkit moz spec);\n}\n\n@mixin transition-delay($times...) {\n  @include prefixer(transition-delay, $times, webkit moz spec);\n}\n","/*------------------------------------*\\\n    Feature Search Bar\n\\*------------------------------------*/\n.et-large-search{\n  .widget_text {\n    margin: 0;\n    display: block;\n    position: relative;\n\n    @include breakpoint(laptop){\n      display: inline-block;\n    }\n\n    .textwidget {\n      font-size: 21px;\n      font-weight: 500;\n      color: $dark-blue;\n    }\n  }\n\n  .wpb_wrapper {\n    text-align: center;\n  }\n\n  .eltdf-search-menu-holder {\n    display: inline-block;\n    width: 90%;\n    //background-color: rgba(80, 80, 80, 0.08);\n    //border: 1px solid rgba(137, 141, 144, 0.24);\n    padding: 10px;\n    border-radius: 50px;\n    overflow: visible;\n    margin: 15px 0 0;\n\n    @include breakpoint(tablet){\n      width: 475px;\n      margin: 15px 25px 0;\n    }\n\n    @include breakpoint(laptop){\n      margin: 0 25px;\n    }\n\n    .eltdf-search-submit {\n      background-color: transparent;\n      width: 100%;\n      height: 54px;\n      padding: 0 30px;\n      border-radius: 50px;\n      border-color: transparent;\n\n      &:hover{\n        background-color: transparent;\n        border-color: transparent;\n\n        .ion-ios-search{\n          color: $cinn;\n        }\n      }\n\n    }\n  }\n\n  .eltdf-column-left{\n    display: block;\n  }\n\n  .eltdf-column-right {\n    display: block;\n    position: absolute;\n    top: 49%;\n    @include transform(translateY(-50%));\n    right: 9px;\n    width: auto;\n  }\n\n  .eltdf-search-field{\n    width: 100%;\n    height: auto;\n    background-color: $white;\n    border-radius: 50px;\n    border: 3px solid $white;\n    padding: 6px 20px 5px;\n    font-size: 16px;\n    font-weight: 300;\n    text-transform: inherit;\n    letter-spacing: 0;\n    -webkit-box-shadow: 0px 18px 44px -17px rgba(0, 0, 0, 0.6);\n    -moz-box-shadow: 0px 18px 44px -17px rgba(0, 0, 0, 0.6);\n  