"use strict";

const $ = require("jquery");
class SearchNavComponent {
    constructor() {
        this.searchBar = $('.et2017-navsearch__bar');
        this.searchBtn = $('.et2017-navsearch__btn');
        this.input = this.searchBar.find('input[type=text]');
        this.isOpen = false;
    }
    searchToggle(e) {
        e.preventDefault();
        if (!this.isOpen) {
            this.isOpen = true;
            this.searchOpen();
        } else {
            this.isOpen = false;
            this.searchClose();
        }
    }
    searchOpen() {
        let animation = new TimelineLite();
        animation.to(this.searchBar, .2, {
            scale: 1,
            display: "block",
            transformOrigin: '50% 50%',
            onComplete: () => {
                this.searchBtn.addClass('active');
            }
        }).to(this.searchBar, .2, {
            y: 23
        }).to(this.searchBar, .2, {
            width: 215,
            onComplete: () => {
                TweenLite.to(this.input, .2, {
                    opacity: "1"
                });
            }
        });
    }
    searchClose() {
        let animation = new TimelineLite();
        animation.to(this.input, 0, {
            opacity: 0,
            onComplete: () => {
                this.searchBtn.removeClass('active');
            }
        }).to(this.searchBar, .2, {
            width: 35
        }).to(this.searchBar, .2, {
            y: -32
        }).to(this.searchBar, .2, {
            scale: 0,
            display: "block"
        });
    }
    init() {
        this.searchBtn.on('click', this.searchToggle.bind(this));
    }
}
let SearchNav = new SearchNavComponent();
Object.defineProperty(exports, "__esModule", { value: true });
exports.default = SearchNav;


